<script>

import TitleVac from '../components/TitleVac.vue'

export default {

    data(){
        return{
            videoPlay: false,
            videoPlay2: false,
            videoPlay3: false,
            videoPlay4: false,
            pagAct: 'pagAct1',
            title: 'Vehicle videos',
        }
    },

    watch: {
        videoPlay: {
            immediate: true,
            handler(val) {
            document.documentElement.classList.toggle('lock', val);
            },
        },
    },
    
    components: {
      TitleVac,
    },
}
</script>
<style lang="scss">
    
@import '../assets/scss/breakpoints.scss';

.video{
    margin: 40px 0;
    @include breakpoints(sm){
        margin: 30px 0;
    }
}
.swiper-video{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 40px;
    @include breakpoints(lg){
        gap: 35px;
    }
    @include breakpoints(md){
        flex-direction: column;
        gap: 40px;
    }
    @include breakpoints(sm){
        gap: 20px;
    }
}
.video-slide {
    width: 48%;
    @include breakpoints(xl){
        width: 47%;
    }
    @include breakpoints(md){
        width: 100%;
    }
}
.video-slide_main {
    h3{
        font-family: 'Gilroy700';
        font-size: 25px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #41456B;
        margin-top: 20px;
        @include breakpoints(sm){
            font-size: 20px;
        }
    }
}
.video-slide_img {
    position: relative;
    cursor: pointer;
    img{
        width: 100%;
        height: 100%;
    }
}
.video-slide_player {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}
.video-play {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.1);
    z-index: 50;

    img{
        width: 24px;
        height: 24px;
        cursor: pointer;
    }
}
.video-play_main{
    display: flex;
    gap: 10px;
    width: 60%;
    height: 60%;
    margin: 200px auto 0;
    @include breakpoints(xl){
        width: 85%;
        height: 55%;
    }
    @include breakpoints(lg){
        width: 88%;
        height: 45%;
    }
    @include breakpoints(md){
        width: 85%;
        height: 40%;
        margin: 175px 24px 0 10%;
        gap: 36px;
    }
    @include breakpoints(sm){
        flex-direction: column-reverse;
        align-items: end;
        gap: 10px;
        margin: 135px 0 0 0;
        padding: 0 20px;
        width: 100%;
    }
}
.video-pagination {
    width: 50px;
    margin: 40px auto 0;
    @include breakpoints(sm){
        margin-top: 30px;
    }
}
.video-pagination_main {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.pagination-span{
    width: 10px;
    height: 10px;
    background: #606276;
    opacity: 0.3;
    border-radius: 50%;
    cursor: pointer;
}
.pagactive{
    opacity: 1;
    transition: .3s opacity ease;
}
</style>